import React from 'react';
import { Link } from 'react-router-dom';
import Web3 from 'web3';
import FrontDAO from '../abi/FrontDAO.json'; // Assicurati che il percorso sia corretto

const Navbar = ({ web3, setWeb3, account, setAccount, contract, setContract }) => {
  const navbarStyle = {
    display: "flex",
    justifyContent: "space-around",
    alignItems: "center",
    backgroundColor: "#333",
    color: "#fff",
    padding: "10px",
    margin: "0 20px",
    position: "fixed",
    top: 0,
    width: "calc(100% - 40px)",
    zIndex: 1000,
  };

  const linkStyle = {
    color: "#fff",
    textDecoration: "none",
    textAlign: "center",
  };

  const connectToMetaMask = async () => {
    if (window.ethereum) {
      window.web3 = new Web3(window.ethereum);
      await window.ethereum.request({method: 'eth_requestAccounts'});
      
      const web3 = window.web3;
      setWeb3(web3);

      const accounts = await web3.eth.getAccounts();
      setAccount(accounts[0]);

      const networkId = await web3.eth.net.getId();
      const networkData = FrontDAO.networks[networkId];
      if (networkData) {
        const contractInstance = new web3.eth.Contract(FrontDAO.abi, networkData.address);
        setContract(contractInstance);
      } else {
        window.alert('Contratto FrontDAO non dispiegato nella rete corrente.');
      }
    }
  }

  return (
    <nav style={navbarStyle}>
      <Link style={linkStyle} to="/">Home</Link>
      <Link style={linkStyle} to="/proposals">Proposte</Link>
      <Link style={linkStyle} to="/archive">Archivio</Link>
      <Link style={linkStyle} to="/create">Crea Proposta</Link>
      <Link style={linkStyle} to="/nfts">NFTs</Link>
      <Link style={linkStyle} to="/membership">Membership</Link> {/* Aggiungi questo */}
      <Link style={linkStyle} to="/admin">Admin</Link>
      {!web3 && !contract ? (
        <button onClick={connectToMetaMask}>Connect</button>
      ) : null}
    </nav>
  );
};

export default Navbar;











