import React, { useState, useEffect } from 'react';
import getWeb3 from '../../src/utilis/getWeb3.js';
import MembershipNFT from '../../src/abi/MembershipNFT.json';
import FrontCoin from '../../src/abi/FrontCoin.json'; // IMPORTA IL CONTRATTO FrontCoin
import BigNumber from 'bignumber.js';

function MembershipNFTPage() {
  const [web3, setWeb3] = useState(undefined);
  const [accounts, setAccounts] = useState([]);
  const [contract, setContract] = useState(undefined);
  const [frontCoinContract, setFrontCoinContract] = useState(undefined); // CONTRATTO FrontCoin
  const [balance, setBalance] = useState(undefined);
  const [price, setPrice] = useState(undefined);
  const [associations, setAssociations] = useState({});
  const [newAssociationName, setNewAssociationName] = useState("");
  const [newMaxMintable, setNewMaxMintable] = useState(0);
  const [error, setError] = useState(undefined);
  const [tokenIds, setTokenIds] = useState([]);

  useEffect(() => {
    const init = async () => {
      try {
        const web3 = await getWeb3();
        const accounts = await web3.eth.getAccounts();
        const networkId = await web3.eth.net.getId();
        const deployedNetwork = MembershipNFT.networks[networkId];
        const instance = new web3.eth.Contract(MembershipNFT.abi, deployedNetwork && deployedNetwork.address);
        const tokenCount = await instance.methods.balanceOf(accounts[0]).call();
      let userTokenIds = [];
      for (let i = 0; i < tokenCount; i++) {
        const tokenId = await instance.methods.tokenOfOwnerByIndex(accounts[0], i).call();
        userTokenIds.push(tokenId);
      }

        // Inizializzazione contratto FrontCoin
        const deployedNetworkFrontCoin = FrontCoin.networks[networkId];
        const frontCoinInstance = new web3.eth.Contract(FrontCoin.abi, deployedNetworkFrontCoin && deployedNetworkFrontCoin.address);

        const balance = await frontCoinInstance.methods.balanceOf(accounts[0]).call(); // OTTIENI SALDO FrontCoin
        const price = await instance.methods.price().call();

        const associationNames = await instance.methods.getAssociationNames().call();
        let associations = {};
        for(let i = 0; i < associationNames.length; i++) {
          const details = await instance.methods.getAssociationDetails(associationNames[i]).call();
          associations[associationNames[i]] = {
            maxMintable: new BigNumber(details[0]),
            currentMinted: new BigNumber(details[1])
          };
        }
        
        setWeb3(web3);
        setAccounts(accounts);
        setContract(instance);
        setFrontCoinContract(frontCoinInstance); // SETTA IL CONTRATTO FrontCoin
        setBalance(balance);
        setPrice(price);
        setAssociations(associations);
        setTokenIds(userTokenIds);
      } catch (error) {
        setError(`Failed to load web3, accounts, or contract. Check console for details.`);
        console.error(error);
      }
    };
    init();
  }, []);

  const addAssociation = async (name, maxMintable) => {
    try {
      await contract.methods.addAssociation(name, maxMintable).send({ from: accounts[0] });
      setAssociations({
        ...associations,
        [name]: {
          maxMintable: new BigNumber(maxMintable),
          currentMinted: new BigNumber(0)
        }
      });
      setNewAssociationName("");
      setNewMaxMintable(0);
    } catch (err) {
      setError(`Failed to add association. Check console for details.`);
      console.error(err);
    }
  };

  const validateInputs = () => {
    // Controllo del nome dell'associazione
    if (!newAssociationName.trim()) {
        alert('Il nome dell\'associazione è obbligatorio.');
        return false;
    }
    const regex = /^[a-zA-Z0-9\s]+$/;
    if (!regex.test(newAssociationName.trim())) {
        alert('Il nome dell\'associazione può contenere solo caratteri alfanumerici e spazi.');
        return false;
    }

    // Controllo della quantità massima stampabile
    if (newMaxMintable <= 0) {
        alert('La quantità massima stampabile deve essere un numero positivo.');
        return false;
    }

    return true;
  }

  const mintNFT = async (recipient, associationName) => {
    try {
      const amount = web3.utils.toWei('1', 'ether'); // Prezzo da approvare, impostalo come necessario
      await frontCoinContract.methods.approve(contract.options.address, amount).send({ from: accounts[0] }); // APPROVA IL CONTRATTO
      await contract.methods.mintNFT(recipient, associationName).send({ from: accounts[0] });
    } catch (err) {
      setError(`Failed to mint NFT. Check console for details.`);
      console.error(err);
    }
  };

  const handleAssociationNameChange = (event) => {
    setNewAssociationName(event.target.value);
  };

  const handleMaxMintableChange = (event) => {
    setNewMaxMintable(event.target.value);
  };

  const addAssociationFormSubmit = (event) => {
    event.preventDefault();
    if(validateInputs()){
      addAssociation(newAssociationName, newMaxMintable);
    }
};

  return (
    <div>
      <h1> Per creare una raccolta di nft per una associazione esterna al Fronte meridionale devi detenere un minimo di 500000 FrontCoin </h1>
      {error && <p>{error}</p>}
      <p>Indirizzo del contratto: {contract && contract.options.address}</p>
      <p><strong>il tuo wallet detiene un nft con id:</strong> {tokenIds.map(tokenId => tokenId.toString()).join(', ')}</p>

      
        
      
      {error && <p>{error}</p>}
      <p>Il tuo saldo di FrontCoin: {web3 && web3.utils.fromWei(balance, 'ether')}</p>
      <p>Prezzo di ogni NFT in FrontCoin : {web3 && web3.utils.fromWei(price, 'ether')}</p>
      {Object.entries(associations).map(([name, { maxMintable, currentMinted }]) => (
        <div key={name}>
          <h2>Association: {name}</h2>
          <p>Minted: {currentMinted.toString()}/{maxMintable.toString()}</p>
          <button onClick={() => mintNFT(accounts[0], name)}>Mint NFT</button>
        </div>
      ))}
      <h2>Add Association</h2>
      <form onSubmit={addAssociationFormSubmit}>
        <label>
          Name:
          <input type="text" value={newAssociationName} onChange={handleAssociationNameChange} />
        </label>
        <label>
          Max mintable:
          <input type="number" value={newMaxMintable} onChange={handleMaxMintableChange} />
        </label>
        <input type="submit" value="Add Association" />
      </form>
    </div>
  );
}

export default MembershipNFTPage;














