import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import Web3 from 'web3';
import FrontDAO from '/home/lupol/front-dao-five/src/abi/FrontDAO.json';
import Navbar from './components/Navbar';
import HomePage from './pages/HomePage';
import ProposalsPage from './pages/ProposalsPage';
import ArchivePage from './pages/ArchivePage';
import CreateProposalPage from './pages/CreateProposalPage';
import ProposalDetailPage from './pages/ProposalDetailPage';
import NFTPage from './pages/NFTPage';
import MembershipNFTPage from './pages/MembershipNFTPage';
import Header from '/home/lupol/front-dao-five/src/components/Header/Header.js';
import AdminInterface from '/home/lupol/front-dao-five/src/pages/admin/AdminInterface.js'; 

function App() {
  const [web3, setWeb3] = useState(null);
  const [account, setAccount] = useState(null);
  const [contract, setContract] = useState(null);
  const [accounts, setAccounts] = useState([]);

  useEffect(() => {
    const loadBlockchainData = async () => {
      // Inizializza web3
      if (window.ethereum) {
        window.web3 = new Web3(window.ethereum);
        await window.ethereum.request({method: 'eth_requestAccounts'});
      } else if (window.web3) {
        window.web3 = new Web3(window.web3.currentProvider);
      } else {
        window.alert('Non-Ethereum browser detected. You should consider trying MetaMask!');
      }

      // Imposta web3 e l'account corrente
      const web3 = window.web3;
      setWeb3(web3);

      const accountsList = await web3.eth.getAccounts();
      setAccount(accountsList[0]);  // imposta il primo account come prima
      setAccounts(accountsList);   // imposta l'intero array

      // Imposta il contratto
      const networkId = await web3.eth.net.getId();
      const networkData = FrontDAO.networks[networkId];
      if (networkData) {
        const contract = new web3.eth.Contract(FrontDAO.abi, networkData.address);
        setContract(contract);
      } else {
        window.alert('Contratto FrontDAO non dispiegato nella rete corrente.');
      }
    };

    loadBlockchainData();
}, []);

  const mainContentStyle = {
    paddingTop: "70px", // Adattare questo valore in base all'altezza del tuo Navbar
  };

  return (
    <Router>
      
          
          <>
          <Header />
          <div style={mainContentStyle}></div>
            <Navbar />
            
            <Routes>
            
              <Route path="/" element={<HomePage account={account} contract={contract} />} />
              <Route path="/proposals" element={<ProposalsPage web3={web3} contract={contract} />} />
              <Route path="/proposals/:id" element={<ProposalDetailPage contract={contract} />} />
              <Route path="/archive" element={<ArchivePage />} />
              <Route path="/create" element={<CreateProposalPage web3={web3} contract={contract} />} />
              <Route path="/nfts" element={<NFTPage web3={web3} contract={contract} />} />
              <Route path="/membership" element={<MembershipNFTPage />} /> {/* Aggiungi questo */}
              <Route path="/admin" element={<AdminInterface contract={contract} accounts={accounts} />} />
            </Routes>
          </>
        
    </Router>
  );
}

export default App;






