import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

const HomePage = ({ account, contract }) => {
  const [proposals, setProposals] = useState([]);

  useEffect(() => {
    const loadProposals = async () => {
      const proposalsData = await contract.methods.getAllProposals().call();
      console.log("Dati proposte caricate:", proposalsData);

      const proposals = proposalsData.ids.map((id, index) => {
        return {
          id: id.toString(),
          proposer: proposalsData.proposers[index],
          description: proposalsData.descriptions[index],
          endTime: new Date(Number(proposalsData.endTimes[index]) * 1000).toLocaleString(),
          yesVotes: proposalsData.yesVotes[index],
          totalVotes: proposalsData.totalVotes[index],
          executed: proposalsData.executeds[index],
          association: proposalsData.associations[index],
          isExternal: proposalsData.isExternals[index],
        };
      });

      console.log("Proposte caricate:", proposals);
      setProposals(proposals);
    };

    if (contract) {
      loadProposals();
    }
  }, [contract]);

  const cardStyle = {
    width: '22rem',
    margin: '5rem',
    boxShadow: '0 4px 8px 0 rgba(0,0,0,0.2)',
    transition: '0.3s',
    borderRadius: '5px'
  };

  return (
    <div>
      <h1>Benvenuti nel web decentralizzato</h1>
      <p>Conto Corrente: {account}</p>

      <div style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'space-between' }}>
        {proposals.map((proposal, index) => (
          <div style={cardStyle} key={index}>
            <h2>Proposta #{proposal.id}</h2>
            <p>{proposal.description}</p>
            <p>{proposal.endTime}</p>
            <p>Voti positivi: {proposal.yesVotes.toString()}</p>
            <p>Voti totali: {proposal.totalVotes.toString()}</p>
            <Link to={`/proposals/${proposal.id}`}>Visualizza dettagli</Link>
          </div>
        ))}
      </div>
    </div>
  );
};

export default HomePage;





