import React, { useState, useEffect } from 'react';
import getWeb3 from '../../src/utilis/getWeb3.js';
import ActivistMembershipNFT from '../../src/abi/ActivistMembershipNFT.json';
import FrontCoin from '../../src/abi/FrontCoin.json'; // Importa il contratto FrontCoin

function NFTPage() {
    const [web3, setWeb3] = useState(undefined);
    const [accounts, setAccounts] = useState([]);
    const [contract, setContract] = useState(undefined);
    const [frontCoinContract, setFrontCoinContract] = useState(undefined);
    const [price, setPrice] = useState(undefined);
    const [tokenId, setTokenId] = useState(undefined);
    const [error, setError] = useState(undefined);
    const [contractAddress, setContractAddress] = useState("");
    const [isMinter, setIsMinter] = useState(false);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
      const init = async () => {
        try {
          const web3 = await getWeb3();
          const accounts = await web3.eth.getAccounts();
          const networkId = await web3.eth.net.getId();
          const deployedNetwork = ActivistMembershipNFT.networks[networkId];
          const instance = new web3.eth.Contract(
            ActivistMembershipNFT.abi,
            deployedNetwork && deployedNetwork.address
          );

          const frontCoinDeployedNetwork = FrontCoin.networks[networkId];
          const frontCoinInstance = new web3.eth.Contract(
            FrontCoin.abi,
            frontCoinDeployedNetwork && frontCoinDeployedNetwork.address
          );

          const currentPrice = await instance.methods.price().call();

          if (deployedNetwork) {
            setContractAddress(deployedNetwork.address);
          }

          // Recupero l'ID dell'NFT posseduto dall'account
          const totalSupply = await instance.methods.totalSupply().call();
          if (totalSupply > 0) {
            try {
                const ownedTokenId = await instance.methods.tokenOfOwnerByIndex(accounts[0], 0).call();
                setTokenId(ownedTokenId.toString());
            } catch (err) {
              console.log('This account does not own any NFTs.');
            }
          }

          setWeb3(web3);
          setAccounts(accounts);
          setContract(instance);
          setFrontCoinContract(frontCoinInstance);
          setPrice(currentPrice);
        } catch (error) {
          setError(`Failed to load web3, accounts, or contract. Check console for details.`);
          console.error(error);
        }
      };
      init();
    }, []);

    useEffect(() => {
        if (contract && accounts.length > 0) {
          const checkRole = async () => {
            try {
              const hasRole = await contract.methods.isMinter(accounts[0]).call();
              setIsMinter(hasRole);
            } catch (err) {
              console.error("Error checking minter role:", err);
            }
          };
          checkRole();
        }
      }, [contract, accounts]); // <-- AGGIUNGI QUESTO

    useEffect(() => {
      const checkMinterRole = async () => {
        try {
          const minterRole = web3.utils.keccak256("MINTER_ROLE");
          const hasRole = await contract.methods.hasRole(minterRole, accounts[0]).call();
          setIsMinter(hasRole);
        } catch (err) {
          console.error("Error checking minter role:", err);
          setError("Errore durante il controllo del ruolo di minter.");
        }
        setLoading(false);
      };

      if (web3 && contract && accounts.length > 0) {
        checkMinterRole();
      }
    }, [web3, contract, accounts]);

    const approveAndPurchaseNFT = async () => {
      try {
        await frontCoinContract.methods.approve(contract.options.address, price).send({ from: accounts[0] });
        await contract.methods.purchase().send({ from: accounts[0] });
      } catch (err) {
        setError(`Failed to approve or purchase NFT. Check console for details.`);
        console.error(err);
      }
    };

    return (
      <div>
        {error && <p>{error}</p>}
        <h1>Questo NFT denominato ActivisterMembershipNFT da la possibilità a chi lo detiene di poter creare proposte di voto </h1>
        <p>Devi essere autorizza per coniare questo nft fanne richiesta clicca qui</p>
        <p>Indirizzo del contratto: {contractAddress}</p>
        <p>Prezzo di ogni NFT in FrontCoin : {web3 && web3.utils.fromWei(price.toString(), 'ether')}</p>
        {tokenId !== undefined && <p>Il tuo ID NFT: {tokenId.toString()}</p>}
        {loading ? (
          <p>Verifica delle autorizzazioni in corso...</p>
        ) : (
          <button onClick={approveAndPurchaseNFT} disabled={!isMinter}>
            {isMinter ? "Approva e Acquista NFT" : "Non autorizzato a comprare"}
          </button>
        )}
      </div>
    );
}

export default NFTPage;








