import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

const ProposalDetailPage = ({ contract }) => {
  const { id } = useParams();
  const [proposal, setProposal] = useState(null);

  useEffect(() => {
    const loadProposal = async () => {
      try {
        const proposalData = await contract.methods.getProposal(id).call();

        console.log(proposalData);

        const proposal = {
          id: id,
          proposer: proposalData[1],
          description: proposalData[2],
          endTime: new Date(Number(proposalData[3]) * 1000).toLocaleString(),
          yesVotes: proposalData[4],
          totalVotes: proposalData[5],
          executed: proposalData[6],
          association: proposalData[7],
          isExternal: proposalData[8],
        };

        setProposal(proposal);
      } catch (error) {
        console.error("There was an error!", error);
      }
    };

    if (contract) {
      loadProposal();
    }
  }, [contract, id]);

  return (
    
    <div>
      {proposal ? (
        <>
          <h1>Proposta #{proposal.id}</h1>
          <p>{proposal.description}</p>
          <p>{proposal.endTime}</p>
          <p>Voti positivi: {proposal.yesVotes.toString()}</p>
          <p>Voti totali: {proposal.totalVotes.toString()}</p>
          {/* ...Altri dettagli della proposta... */}
        </>
      ) : (
        <p>Caricamento dettagli della proposta...</p>
      )}
    </div>
  );
};

export default ProposalDetailPage;





