import React, { useState, useEffect, useMemo } from 'react';
import Web3 from 'web3';
import ActivistMembershipNFT_ABI from '../../abi/ActivistMembershipNFT.json';

function AdminInterface() {
    const [address, setAddress] = useState('');
    const [web3] = useState(new Web3(window.ethereum));
    const ABI = ActivistMembershipNFT_ABI.abi;
    const contractAddress = '0xDD8bDf18940E5d60De465f4f9d930fD7d10Fb665';
    const [minters, setMinters] = useState([]); // Aggiunto per conservare la lista dei Minter

    const contract = useMemo(() => new web3.eth.Contract(ABI, contractAddress), [web3, ABI, contractAddress]);

    const [isOwner, setIsOwner] = useState(false);

    useEffect(() => {
      async function fetchMinters() {
          const minterGrantedEvents = await contract.getPastEvents('RoleGranted', { fromBlock: 0 });
          const minterRevokedEvents = await contract.getPastEvents('RoleRevoked', { fromBlock: 0 });

          const grantedAddresses = minterGrantedEvents.map(event => event.returnValues.account);
          const revokedAddresses = minterRevokedEvents.map(event => event.returnValues.account);

          const activeMinters = grantedAddresses.filter(address => !revokedAddresses.includes(address));

          // Rimuove i duplicati
          const uniqueMinters = [...new Set(activeMinters)];

          setMinters(uniqueMinters);
      }

      async function checkOwner() {
          const accounts = await web3.eth.getAccounts();
          const contractOwner = await contract.methods.owner().call();
          setIsOwner(accounts[0].toLowerCase() === contractOwner.toLowerCase());
      }

      fetchMinters();
      checkOwner();
  }, [web3, contract]);


    async function handleGrantRole() {
        try {
            const accounts = await web3.eth.getAccounts();
            await contract.methods.grantMinter(address).send({ from: accounts[0] });
            alert('Ruolo assegnato con successo!');
            setMinters(prevMinters => [...prevMinters, address]); // Aggiungi l'indirizzo all'elenco dei Minter
        } catch (error) {
            console.error('Errore durante l\'assegnazione del ruolo:', error);
        }
    }
    async function handleRevokeRole() {
      try {
          const accounts = await web3.eth.getAccounts();
          await contract.methods.revokeMinter(address).send({ from: accounts[0] });
          alert('Ruolo revocato con successo!');
          // Rimuove l'indirizzo dall'elenco dei Minter
          setMinters(prevMinters => prevMinters.filter(minter => minter !== address));
      } catch (error) {
          console.error('Errore durante la revoca del ruolo:', error);
      }
  }

  async function handleCheckRole() {
      try {
          const hasRole = await contract.methods.isMinter(address).call();
          if (hasRole) {
              alert('L\'indirizzo ha il ruolo MINTER_ROLE');
          } else {
              alert('L\'indirizzo NON ha il ruolo MINTER_ROLE');
          }
      } catch (error) {
          console.error('Errore durante il controllo del ruolo:', error);
      }
  }

    // Resto del tuo codice...

    if (!isOwner) {
      return <div>Accesso non autorizzato.</div>;
    }

    return (
        <div>
            <input 
                type="text" 
                placeholder="Indirizzo Ethereum"
                value={address}
                onChange={e => setAddress(e.target.value)} 
            />
            <button onClick={handleGrantRole}>Assegna Ruolo</button>
            <button onClick={handleRevokeRole}>Revoca Ruolo</button>
            <button onClick={handleCheckRole}>Controlla Ruolo</button>
            <div>
                <h2>Elenco dei Minter</h2>
                <ul>
                    {minters.map(minter => <li key={minter}>{minter}</li>)} {/* Mostra l'elenco dei Minter */}
                </ul>
            </div>
        </div>
    );
}

export default AdminInterface;




