import React, { useState } from 'react';

const Proposal = ({ proposal, contract, web3 }) => {
  const [hasVoted, setHasVoted] = useState(false);

  const handleVote = async (support) => {
    const accounts = await web3.eth.getAccounts();
    await contract.methods.vote(proposal.id, support).send({ from: accounts[0] });
    console.log(`Voto ${support ? 'a favore' : 'contro'} inviato per la proposta ${proposal.id}`);
    setHasVoted(true);
  };

  return (
    <div>
      <h2>Proposta con id {proposal.id.toString()}</h2>
      <p>Proposta da: {proposal.proposer}</p>
      <p>Descrizione: {proposal.description}</p>
      <p>Data di fine: {new Date(Number(proposal.endTime) * 1000).toLocaleString()}</p>
      <p>Voti a favore: {proposal.yesVotes.toString()}</p>
      <p>Voti totali ricevuti: {proposal.totalVotes.toString()}</p>
      <p>Eseguita: {proposal.executed ? 'Sì' : 'No'}</p>
      <p>Associazione: {proposal.association}</p>
      <p>Esterna: {proposal.isExternal ? 'Sì' : 'No'}</p>
      {!hasVoted && (
        <div>
          <button onClick={() => handleVote(true)}>Vota a favore</button>
          <button onClick={() => handleVote(false)}>Vota contro</button>
        </div>
      )}
    </div>
  );
};

export default Proposal;



