import React, { useState, useEffect } from 'react';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import Proposal from './Proposal';

const DAOContract = ({ web3, contract }) => {
  const [proposals, setProposals] = useState([]);
  const [newProposalDescription, setNewProposalDescription] = useState('');
  const [newProposalEndTime, setNewProposalEndTime] = useState(new Date());
  const [newProposalAssociation, setNewProposalAssociation] = useState('');
  const [newProposalIsExternal, setNewProposalIsExternal] = useState(false);

  // Carica le proposte esistenti quando il componente viene montato
  useEffect(() => {
    async function loadProposals() {
      // getAllProposals ritorna già tutte le proposte
      const loadedProposals = await contract.methods.getAllProposals().call();
  
      console.log('Proposte caricate:', loadedProposals);

      const proposals = loadedProposals.ids.map((id, index) => {
        return {
          id: id,
          proposer: loadedProposals.proposers[index],
          description: loadedProposals.descriptions[index],
          endTime: Number(loadedProposals.endTimes[index]),
          yesVotes: loadedProposals.yesVotes[index],
          totalVotes: loadedProposals.totalVotes[index],
          executed: loadedProposals.executeds[index],
          association: loadedProposals.associations[index],
          isExternal: loadedProposals.isExternals[index],
        };
      });

      setProposals(proposals);
    }
  
    loadProposals();
  }, [contract]);

  const handleNewProposal = async () => {
    // Convert the selected end time to a Unix timestamp
    const endTimeUnix = Math.floor(newProposalEndTime.getTime() / 1000);

    // Send the transaction to create a new proposal
    const accounts = await web3.eth.getAccounts();
    await contract.methods.createProposal(newProposalDescription, endTimeUnix, newProposalAssociation, newProposalIsExternal).send({ from: accounts[0] });

    // Aggiorna l'elenco delle proposte
    const newProposal = { id: proposals.length, proposer: accounts[0], description: newProposalDescription, endTime: newProposalEndTime, yesVotes: 0, totalVotes: 0, executed: false, association: newProposalAssociation, isExternal: newProposalIsExternal };
    console.log('New proposal created:', newProposal);
    setProposals([...proposals, newProposal]);
    setNewProposalDescription('');
    setNewProposalEndTime(new Date());
    setNewProposalAssociation('');
    setNewProposalIsExternal(false);
  };

  return (
    <div>
      {proposals.map((proposal, index) => (
        <Proposal key={index} proposal={proposal} contract={contract} web3={web3} />
      ))}

      <h2>Crea una nuova proposta</h2>
      <input type="text" value={newProposalDescription} onChange={e => setNewProposalDescription(e.target.value)} placeholder="Descrizione" />
      <DatePicker selected={newProposalEndTime} onChange={date => setNewProposalEndTime(date)} dateFormat="dd/MM/yyyy" />

      <input type="text" value={newProposalAssociation} onChange={e => setNewProposalAssociation(e.target.value)} placeholder="Associazione" />
      <label>
    <input 
        type="checkbox" 
        checked={newProposalIsExternal} 
        onChange={e => setNewProposalIsExternal(e.target.checked)} 
    />
    Esterno (Seleziona questa opzione se la proposta è esterna all'associazione)
</label>
      <button onClick={handleNewProposal}>Crea proposta</button>
    </div>
  );
};

export default DAOContract;







